import { Component } from '@angular/core';
import {  ActionSheetController  ,AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-plantilladmc-list',
  templateUrl: 'plantilladmc-list.html',
})
export class PlantilladmcListPage {

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  generated = '';
  idplantilla: any;
  idevento: any;
  idempresa: any;
  nevento: any;
  ubica: any;
  fecha: any;
  idafiliado:any='na';
  public hasFilter: boolean = false;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";

  constructor( public modalCtrl : ModalController,public actionSheetCtrl: ActionSheetController
    ,public storage: Storage,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });   
  }



  pideregistros() {    
      let self = this;   
      self.loading = true;
     
      this.regService.getReg(this.idplantilla + '/' +  this.idempresa +'/'+this.idevento + '/todos' ,'plantilladmcs').then((data) => {
 
      this.items=data;
      this.items2=data;
      self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
  });
  }
  ionViewDidLoad() {
    this.idplantilla=this.navParams.data.record2.idplantilla;
    this.idevento=this.navParams.data.record2.idplantillam;
    this.nevento=this.navParams.data.record2.nombrep;
    this.idempresa=this.navParams.data.record2.idempresa;

    this.pideregistros();


  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }

  
  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'plantilladmcs').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


  
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {

    let addModal = this._MODAL .create('PlantilladmcCreatePage',  { record : item ,record2:{idplantillam: this.navParams.data.record2.idplantillam,idplantilla:this.navParams.data.record2.idplantilla,nidevento:this.nevento,idempresa:this.idempresa}},{showBackdrop: true,enableBackdropDismiss: false,cssClass: "modal-fullscreen"});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
        this.items2=this.items
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void   
   {
    let addModal = this._MODAL .create('PlantilladmcCreatePage', { record : null ,record2:{idplantillam: this.navParams.data.record2.idplantillam,idplantilla:this.navParams.data.record2.idplantilla,nidevento:this.nevento,idempresa:this.idempresa}},{showBackdrop: true,enableBackdropDismiss: false,cssClass: "modal-fullscreen"});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
        this.items2=this.items
      }  
    })
    addModal.present();
      //this.navCtrl.push('EmpresaCreatePage');
   }
   viewRecord(item : any): void
   {
        
   }


   presentFilter() {
    
  }   

  addimagen() : void
  {
/*
    let addModal = this._MODAL .create('GaleriaimgListPage', {idempresa:this.idempresa},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
      
      }
    })    
    addModal.present();
    */
    this.navCtrl.push('GaleriaimgListPage',{idempresa:this.idempresa});

  }


}



